body {
  margin: 0;
  padding: 0;
}

.App {
  background-color: lightgray;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.Form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: flex-start;
  background-color: white;
  border: 1px solid white;
  margin: 20px;
  padding: 20px;
  box-shadow: 0 0 10px;
  border-radius: 10px;
  width: 400px;
}
